.link-button-menu {
    background-color: #333;
    color: rgb(255, 255, 255);
    padding: 10px 15px;
    border: 2px solid black;
    box-shadow: 0 0 0 2px white inset;
    border-radius: 1rem;
    margin: 2px;
}

.link-button-menu:hover {
    background-color: black;
    color: #fff;
    cursor: pointer;
}

/* ======================================================================================================= */

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #333;
    color: #fff;
    border: 2px solid black;
    box-shadow: 0 0 0 2px white inset;
    border-radius: 1rem;
}

.user-info {
    font-weight: bold;
}

.link-button-sign {
    background-color: #ffc107;
    color: rgb(0, 0, 0);
    padding: 10px 20px;
    border: 1px solid white;
    box-shadow: 0 0 0 2px black inset;
    /* horizontal shadow, vertical shadow, blurr radius, spread radius of shadow, shadow color, Shadow appear inside the element border */
    border-radius: 10px;
    margin: 2px;
}

.link-button-sign:hover {
    background-color: #987305;
    color: #fff;
}

/* ======================================================================================================= */

.tool-bar {
    display: flex;
    align-items: center;
    padding: 10px;
    background-color: #fff4b8;
    margin: 4px;
    border: 2px solid black;
    box-shadow: 0 0 0 2px rgb(255, 255, 255) inset;
    border-radius: 1rem;
}

@media screen and (max-width: 600px) {
    .tool-bar {
        flex-wrap: wrap;
        justify-content: center;
    }
}

.tool-bar-input {
    margin-right: 10px;
}

.tool-bar-button {
    margin-right: 10px;
    padding: 8px 12px;
    background-color: #007bff;
    color: #fff;
    border: 2px solid black;
    box-shadow: 0 0 0 2px white inset;
    border-radius: 1rem;
    cursor: pointer;
}

.tool-bar-button:hover {
    background-color: #0056b3;
}

/* ======================================================================================================= */

.side-menu-bar {
    position: fixed;
    top: 0;
    left: -300px;
    /* Start off screen */
    width: 300px;
    height: 100%;
    background-color: #333;
    transition: left 0.3s ease;
    /* Smooth transition animation */
    z-index: 1000;
    /* Ensure it's above other elements */
    overflow: auto;
}

.show-menu {
    left: 0;
    /* Show the menu */
}

.link-button-menu-in {
    background-color: #333;
    color: rgb(255, 255, 255);
    padding: 10px 15px;
    border: 1px solid #07f7ff;
    border-radius: 1rem;
    margin-top: 15px;
}

.link-button-menu-in:hover {
    background-color: black;
    color: #fff;
    cursor: pointer;
}


.menu-content-wrapper {
    padding-left: 5px;
    color: #fff;
    border: 1px solid #07f7ff;
    border-radius: 1rem;
    margin: 5px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.menu-content-left {
    flex-grow: 1;
    overflow: hidden;
}

.menu-content-wrapper:hover {
    background-color: black;
    cursor: pointer;
}

.link-button-delete {
    background-color: #fbfffc;
    color: rgb(0, 0, 0);
    padding: 5px 10px;
    border: 1px solid white;
    box-shadow: 0 0 0 2px black inset;
    border-radius: 10px;
    margin: 2px;
}

.link-button-delete:hover {
    background-color: #ff0000;
}

/* ======================================================================================================= */

.display-content-div {
    background-color: #f0f0f0;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    text-align: center;
}

